import { Component, OnInit, Inject } from '@angular/core';
import {AngularFireDatabase} from '@angular/fire/database';
import { map } from 'rxjs/operators';
import { StorageService, LOCAL_STORAGE } from 'ngx-webstorage-service';

@Component({
  selector: 'app-goals',
  templateUrl: './goals.component.html',
  styleUrls: ['./goals.component.scss']
})
export class GoalsComponent implements OnInit {
  weekdays = [
    {dayNumber: 1, dayName: "Mon", selected: true},
    {dayNumber: 2, dayName: "Tues", selected: false},
    {dayNumber: 3, dayName: "Wed", selected: false},
    {dayNumber: 4, dayName: "Thrs", selected: false},
    {dayNumber: 5, dayName: "Fri", selected: false},
    {dayNumber: 6, dayName: "Sat", selected: false},
    {dayNumber: 7, dayName: "Sun", selected: false}
];

mondayGoals = [];
tuesdayGoals = [];
wednesdayGoals = [];
thursdayGoals = [];
fridayGoals = [];
saturdayGoals = [];
sundayGoals = [];

goalsArray : any[];
testingArray: any;
goalName : string;
playerId = "";
  constructor(private db: AngularFireDatabase, @Inject(LOCAL_STORAGE) private storage: StorageService) {
    if(storage.get("currentPlayer")){
      this.playerId = storage.get("currentPlayer").playerKey;
      this.testingArray = db.list("/goals", ref=> ref.orderByChild("playerId").equalTo(this.playerId));
      this.testingArray.snapshotChanges().pipe(
        map((changes:any) =>
          changes.map(c =>
            ({ key: c.payload.key, ...c.payload.val() })
          )
        )).subscribe(result => {
          console.log({queryResult: result});
          this.goalsArray = result;

          this.mondayGoals = this.goalsArray.filter(x=> x.dayOfWeek == 1);
          this.tuesdayGoals = this.goalsArray.filter(x=> x.dayOfWeek == 2);
          this.wednesdayGoals = this.goalsArray.filter(x=> x.dayOfWeek == 3);
          this.thursdayGoals = this.goalsArray.filter(x=> x.dayOfWeek == 4);
          this.fridayGoals = this.goalsArray.filter(x=> x.dayOfWeek == 5);
          this.saturdayGoals = this.goalsArray.filter(x=> x.dayOfWeek == 6);
          this.sundayGoals = this.goalsArray.filter(x=> x.dayOfWeek == 7);                  
        });
      }
   }

  ngOnInit() {
  }

  selectDay(dayOfWeek){
    this.weekdays.forEach((item)=>{
      if(item.dayNumber == dayOfWeek.dayNumber){
        item.selected = !item.selected;
      }
    });
  }

  

  addGoal(goalName){
    let selectedDays = this.weekdays.filter(x=> x.selected);
    selectedDays.forEach((day)=> {
      let goalObject = {
        name: goalName,
        playerId: this.playerId,
        dayOfWeek: day.dayNumber
      };
      this.db.list("goals").push(goalObject);
      this.goalName = "";
    });
  }

  completeGoal(goal){
    console.log({goalToUpdate: goal});
    goal.completed = !goal.completed;
    this.db.list('goals').update(goal.key, goal);
  }




  

}
