import { Component, OnInit } from '@angular/core';
import {AngularFireDatabase} from '@angular/fire/database';
import { map } from 'rxjs/operators';
import { Inject, Injectable } from '@angular/core';
import { LOCAL_STORAGE, StorageService } from 'ngx-webstorage-service';

@Component({
  selector: 'app-ranking',
  templateUrl: './ranking.component.html',
  styleUrls: ['./ranking.component.scss']
})
export class RankingComponent implements OnInit {

  playerList = [];
  goalsList = [];
  storageKey = "currentPlayer";
  playerSaved = false;
  buttonState : any;
//IoT
  




  constructor(private db: AngularFireDatabase,
     @Inject(LOCAL_STORAGE) private storage: StorageService) { 
    this.playerSaved = this.storage.get(this.storageKey) ? true: false;
    db.list("/goals")
    .snapshotChanges().pipe(
      map((changes:any) =>
        changes.map(c =>
          ({ key: c.payload.key, ...c.payload.val() })
        )
      )).subscribe((result)=> {
        this.goalsList = result;
        this.getPlayers();

      });


      this.buttonState = db.object("buttonState");
      

      

  }

  getPlayers(){
    this.db.list("/players")
    .snapshotChanges().pipe(
      map((changes:any) =>
        changes.map(c =>
          ({ key: c.payload.key, ...c.payload.val() })
        )
      )).subscribe((result)=> {
        this.playerList = [];
        result.forEach((player:any)=>{
         
          this.playerList.push({
            "playerName": player.nickname,
            "points": this.getPoints(player.key)
          });
        });

        this.playerList.sort((a,b)=> {
          return b.points  - a.points;
        });

        
      });
  }

  getPoints(playerKey){
    var totalPlayerPoints = 0;
    console.log({playerKey: playerKey,
      goals: this.goalsList
    });
      this.goalsList.forEach((goal:any)=>{
        if(goal.completed && goal.playerId == playerKey)
          totalPlayerPoints += 5;
      });
      return totalPlayerPoints;    
  }

  addPlayer(playerName){
    // push new task to array
    if(!this.playerList.find(x=> x.nickname == playerName)){
      this.db.list("/players").push({nickname: playerName}).then((snapshot)=>{
        if(!this.storage.get(this.storageKey))
          this.storage.set(this.storageKey, {
            nickname: playerName,
            playerKey: snapshot.key
        });
        window.location.reload();
      });
    }
    
    
    this.playerSaved = true;
  }


  clickme(){

    this.db.list("/buttonState").query.once("value").then((value)=>{
      console.log();
      this.buttonState.set(!value.val());
    });   
   
  }


  ngOnInit() {
  }

}
